<template>
    <div>
        <v-header>
            <div slot="title" class="search clear-fix">
                <span class="img"></span>
                <input type="text" v-model="keyword" v-link="{name:'search-keyword',query:{keyword:keyword}}"
                       class="keyword button-style" placeholder="/" readonly="readonly">
            </div>
        </v-header>
        <div :class="['search-box','z-index-1',{'b-alone':standalone}]">
            <div class="warp">
                <ul class="search-ul dis-flex">
                    <li class="service-type" @click="searchUp($event,1)"><span>{{service.name}}</span></span>
                    </li>
                    <li class="area" @click="searchUp($event,2)"><span>{{area | area-filter}}</span></li>
                    <li class="sort" @click="searchUp($event,3)"><span>{{sortName}}</span></li>
                    <li class="filter" @click="searchUp($event,4)"><span class="{{isFilter}}"></span>
                    </li>
                </ul>
            </div>
        </div>
        <div :class="['search-form','z-index-1',{'active':inSearch},{'b-alone':standalone}]">
            <div class="warp">
                <div v-show="searchType == 1" class="search-list" style="height:100%;">
                    <ul class="left-menu menu">
                        <li :class="[parent.serviceId == 0 ? 'active':'']"
                            @click="searchDown(0,'')">
                        </li>
                        <li v-for="item in parents" @click="getChildren(item.serviceId)"
                            :class="[item.serviceId == parent.serviceId ? 'active':'']">
                            <span class="img" style="background-image:url({{item.icon | img-host}})"></span>
                            {{item.name}}
                        </li>
                    </ul>
                    <ul class="right-menu menu">
                        <li @click="searchDown(parent.serviceId,parent.name)"
                            :class="[service.serviceId == parent.serviceId ? 'active':'']"
                            v-show="parent.serviceId != 0 && parent.serviceId != 99">
                        </li>
                        <li v-for="item in children"
                            :class="[service.serviceId == item.serviceId ? 'active':'']"
                            @click="searchDown(item.serviceId,item.name)">
                            {{item.name}}
                        </li>
                    </ul>
                </div>
                <ul v-show="searchType == 2" class="search-list">
                    <li v-for="item in areaList" @click="searchDown(item.code,item.name)"
                        :class="[item.code==area ? 'color-main' : '']">
                        {{item.name}}
                    </li>
                </ul>
                <ul v-show="searchType == 3" class="search-list">
                    <li v-for="item in sortList" @click="searchDown(item.id,item.name)"
                        :class="[item.id==sort ? 'color-main' : '']">
                        {{item.name}}
                    </li>
                </ul>
            </div>
        </div>
        <div :class="['filter-form','z-index-1',{'b-alone':standalone}]" v-show="inFilter">
            <div class="warp">
                <form class="form1" onsubmit="return false">
                    <p></p>
                    <input type="text" v-model="price1" onkeyup="value=value.replace(/[^\d]/g,'')"/>
                    <span></span> <span>-</span>
                    <input type="text" v-model="price2" onkeyup="value=value.replace(/[^\d]/g,'')"/>
                    <span></span>

                    <p></p>
                    <div class="date-box">
                        <p>
                            <input type="text" placeholder="" v-model="startDateFmt" readonly="readonly"
                                   @click="selectDate(1)">
                            <span class="down2"></span>
                        </p>
                    </div>

                    <p></p>
                    <div class="date-box">
                        <p>
                            <input type="text" placeholder="" readonly="readonly" v-model="endDateFmt"
                                   @click="selectDate(2)"/>
                            <span class="down2"></span>
                        </p>
                    </div>

                    <p></p>
                    <div id="attes" class="tip-box">
                        <span :class="['tip',attes==-1 ? 'active' : '']" @click="attesChange(-1,$event)"></span>
                        <span :class="['tip',attes==1 ? 'active' : '']" @click="attesChange(1,$event)"></span>
                        <span :class="['tip',attes==0 ? 'active' : '']" @click="attesChange(0,$event)"></span>
                    </div>

                    <p><span class="color-9 font-12">()</span></p>
                    <div id="periods" class="tip-box">
            <span :class="['tip noraml',periods.normal ? 'active' : '']"
                  @click="periodsChange('normal',$event)"></span>
                        <span :class="['tip am',periods.am ? 'active' : '']"
                              @click="periodsChange('am',$event)"></span>
                        <span :class="['tip pm',periods.pm ? 'active' : '']"
                              @click="periodsChange('pm',$event)"></span>
                        <span :class="['tip em',periods.em ? 'active' : '']"
                              @click="periodsChange('em',$event)"></span>
                    </div>

                    <p></p>
                    <div id="serviceDay" class="tip-box">
                        <span :class="['tip',serviceDay==-1 ? 'active' : '']"
                              @click="serviceDayChange(-1,$event)"></span>
                        <span :class="['tip',serviceDay==1 ? 'active' : '']"
                              @click="serviceDayChange(1,$event)"></span>
                        <span :class="['tip',serviceDay==2 ? 'active' : '']"
                              @click="serviceDayChange(2,$event)"></span>
                    </div>

                    <p></p>
                    <div id="sex" class="tip-box">
                        <span :class="['tip',sex==-1 ? 'active' : '']" @click="sexChange(-1,$event)"></span>
                        <span :class="['tip',sex==1 ? 'active' : '']" @click="sexChange(1,$event)"></span>
                        <span :class="['tip',sex==0 ? 'active' : '']" @click="sexChange(0,$event)"></span>
                    </div>

                    <p></p>
                    <div id="age" class="tip-box">
                        <span :class="['tip',age==-1 ? 'active' : '']" @click="ageChange(-1,$event)"></span>
                        <span :class="['tip',age==1 ? 'active' : '']" @click="ageChange(1,$event)">25</span>
                        <span :class="['tip',age==2 ? 'active' : '']" @click="ageChange(2,$event)">25-35</span>
                        <span :class="['tip',age==3 ? 'active' : '']" @click="ageChange(3,$event)">35</span>
                    </div>

                </form>
                <v-footer-btn style="position:fixed;">
                    <input type="button" class="input small reset left" value="" @click="filterReset"/>
                    <input type="button" class="input mid ok right" value="" @click="filterDown"/>
                </v-footer-btn>
            </div>
        </div>
        <v-date-component></v-date-component>
        <v-content :type="'list'">
            <v-service-list class="service-list" :link_name="'list'" :service_Id="serviceId" :sort="sort"
                            :area="area" :filter="filter" :keyword="keyword">
            </v-service-list>
        </v-content>
    </div>
</template>
<script>
    import VHeader from '../components/Header'
    import VContent from '../components/Content'
    import VFooterBtn from '../components/FooterBtn'
    import VServiceList from '../components/ServiceList'
    import VDateComponent from '../components/DateComponent'
    import {area} from '../assets/js/city'
    import {consts, url} from '../const'
    import $ from 'jquery'
    import {toast} from '../assets/js/toast'
    import {dateFmtCHS} from '../filter'
    import {DJUtils} from '../assets/js/common'

    export default{
        route: {
            data({from, to}){
                if (this.isReady && from.name != 'service-detail' && from.name != 'service-list') {
                    return {
                        state: !this.state
                    }
                }
            }
        },
        ready(){
            let vm = this;
            vm.$on('ready', vm.init);
            vm.loadMenu();
            let year = new Date().getFullYear();
            for (let i = (year - 1); i < (year + 2); i++) {
                vm.years.push({'id': parseInt(i), 'name': parseInt(i)});
            }
            for (let i = 1; i < 32; i++) {
                vm.days1.push({'id': parseInt(i), 'name': parseInt(i)});
                vm.days2.push({'id': parseInt(i), 'name': parseInt(i)});
            }
            for (let i = 1; i < 13; i++) {
                vm.months.push({'id': parseInt(i), 'name': parseInt(i)});
            }
            vm.$emit('ready');
            vm.isReady = true;
            this.standalone = DJUtils.isStandalone();
        },
        data(){
            return {
                isReady: false,
                state: false,
                //
                serviceId: 0,
                area: '330100',
                sort: 0,
                filter: {
                    price1: this.price1,
                    price2: this.price2,
                    startTime: '',
                    endTime: '',
                    period: '',
                    sex: this.sex,
                    attes: this.attes,
                    minAge: 0,
                    maxAge: 0,
                    serviceDay: this.serviceDay
                },
                keyword: '',
                //
                parent: {},
                parents: [],
                children: [],
                category: [],
                service: {name: '', serviceId: 0},

                sortList: consts.sortList,
                areaList: [],
                sortName: '',
                isFilter: '',
                inSearch: false,
                searchType: 0,
                //filter
                inFilter: false,
                price1: '',
                price2: '',
                startYear: 0,
                startMonth: 0,
                startDay: 0,
                endYear: 0,
                endMonth: 0,
                endDay: 0,
                period: [],
                periods: {normal: true, am: false, pm: false, em: false},
                serviceDay: -1,
                sex: -1,
                attes: -1,
                age: -1,
                years: [],
                months: [],

                startDate: '',
                endDate: '',
                startDateFmt: '',
                endDateFmt: '',

                days1: [],
                days2: [],
                currFilter: {},
                standalone: false
            }
        },
        methods: {
            init(){
                let vm = this;
                let filterProps = ['price1', 'price2', 'startTime', 'endTime', 'period', 'sex', 'attes', 'minAge', 'minAge', 'serviceDay'];
                //
                let query = vm.$route.query;
                let t = /^(\+|-)?\d+($|\.\d+$)/;
                for (let i in query) {
                    if (i == 'id' || i == 'name') {
                        continue;
                    }
                    if (query[i]) {
                        if (filterProps.indexOf(i) > -1) {
                            if (t.test(query[i]) && i != 'period') {
                                vm.filter[i] = parseInt(query[i]);
                            } else {
                                vm.filter[i] = query[i];
                            }
                        } else {
                            if (t.test(query[i]) && i != 'area' && i != 'keyword') {
                                vm[i] = parseInt(query[i]);
                            } else {
                                vm[i] = query[i];
                            }
                        }
                    }
                    if (i == 'sex' && query[i] == '0') {
                        vm.filter[i] = 0;
                    }
                    if (i == 'attes' && query[i] == '0') {
                        vm.filter[i] = 0;
                    }
                }
                if (vm.sort > 0) {
                    vm.sortName = consts.sortList[vm.sort].name;
                }

                if (!query.id) {
                    vm.serviceId = 0;
                    vm.service = {name: '', serviceId: 0};
                } else {
                    vm.serviceId = parseInt(query.id);
                    vm.service = {name: query.name, serviceId: query.id};
                }

                vm.areaList = area.getDistrictList(vm.area);

                vm.initFilter();
                if (vm.isReady) {
                    vm.doEvent(true);
                }
            },
            searchUp(event, type){
                let vm = this;
                let li = $(event.currentTarget);
                let _type = vm.searchType;
                console.log('_type:' + _type);
                if (_type != 0) {//box
                    if (_type == type) {//box
                        li.removeClass('active');
                        vm.searchType = 0;
                        if (type == 4) {
                            vm.inFilter = false;
                        } else {
                            vm.inSearch = false;
                        }
                        toast.hide_mask();
                    } else {//box
                        if (type == 1) {
                            vm.getChildren(vm.service.serviceId);
                        }
                        li.addClass('active').siblings().removeClass('active');
                        vm.searchType = type;
                        if (_type == 4) {//type==4 box
                            vm.inFilter = false;
                            vm.inSearch = true;
                        } else if (type == 4) {//type==4 box
                            vm.inFilter = true;
                            vm.inSearch = false;
                        }
                        toast.mask(function () {
                            vm.doEvent();
                        });
                    }
                } else {//box
                    if (type == 1) {
                        vm.getChildren(vm.service.serviceId);
                    }
                    li.addClass('active');
                    vm.searchType = type;
                    type == 4 ? vm.inFilter = true : vm.inSearch = true;
                    if (type != 4) {
                        toast.mask(function () {
                            vm.doEvent();
                        });
                    }
                }
            },
            searchDown(id, name){
                let _type = this.searchType;
                console.log('type:' + _type + ' id:' + id + ' name:' + name);
                if (_type == 1) {
                    this.service = {name: name, serviceId: id};
                    this.serviceId = id;
                } else if (_type == 2) {
                    this.area = id;
                } else {
                    this.sort = id;
                    this.sortName = name;
                }
                this.doEvent(true);
            },
            filterDown(){
                //
                if (this.startDate) {
                    this.filter.startTime = this.startDate;
                }
                if (this.endDate) {
                    this.filter.endTime = this.endDate;
                }
                if (this.startDate && this.endDate && DJUtils.stringToDate(this.startDate) > DJUtils.stringToDate(this.endDate)) {
                    toast.alert('');
                    return;
                }

                this.filter.price1 = this.price1;
                this.filter.price2 = this.price2;
                //
                let p = this.periods;
                this.period = [];
                if (p.normal) {
                    this.period = [-1];
                } else {
                    if (p.am) {
                        this.period.push(1);
                    }
                    if (p.pm) {
                        this.period.push(2);
                    }
                    if (p.em) {
                        this.period.push(3);
                    }
                }
                this.filter.period = this.period.join(',');

                this.filter.sex = this.sex;
                this.filter.attes = this.attes;

                if (this.age == 1) {
                    this.filter.minAge = 0;
                    this.filter.maxAge = 25;
                } else if (this.age == 2) {
                    this.filter.minAge = 25;
                    this.filter.maxAge = 35;
                } else if (this.age == 3) {
                    this.filter.minAge = 35;
                    this.filter.maxAge = 0;
                } else {
                    this.filter.minAge = 0;
                    this.filter.maxAge = 0;
                }

                this.filter.serviceDay = this.serviceDay;
                this.doEvent(true);
            },
            filterReset(){
                $(".filter-form .form1")[0].reset();
                this.price1 = null;
                this.price2 = null;
                this.startDate = '';
                this.endDate = '';
                this.sex = -1;
                this.attes = -1;
                this.age = -1;
                this.serviceDay = -1;
                this.periods = {normal: true, am: false, pm: false, em: false};
                this.filter = {};
            },
            doEvent(isRefs){
                this.searchType = 0;
                this.inSearch = false;
                this.inFilter = false;
                $(".search-ul li").removeClass('active');
                toast.hide_mask();
                if (isRefs) {
                    let _filter = this.filter;
                    let link = {
                        sort: this.sort,
                        id: this.service.serviceId,
                        name: this.service.name,
                        area: this.area,
                        keyword: this.keyword,
                        price1: _filter.price1,
                        price2: _filter.price2,
                        startTime: _filter.startTime,
                        endTime: _filter.endTime,
                        period: _filter.period,
                        sex: _filter.sex,
                        attes: _filter.attes,
                        minAge: _filter.minAge,
                        maxAge: _filter.maxAge,
                        serviceDay: _filter.serviceDay
                    };
                    let url = '#/service/list?';
                    for (let i in link) {
                        if (link[i] && link[i] != -1) {
                            url += (i + '=' + link[i]) + "&";
                        }
                        if (link[i] == 0 && (i == 'sex' || i == 'id')) {
                            url += (i + '=' + link[i]) + "&";
                        }
                        if (link[i] == 0 && (i == 'attes' || i == 'id')) {
                            url += (i + '=' + link[i]) + "&";
                        }
                    }
                    window.location.href = url;
                    this.$broadcast('refs');
                }
            },
            //
            ageChange(val, event){
                let obj = $(event.currentTarget);
                if (!obj.is('.active')) {
                    this.age = val;
                }
            },
            sexChange(val, event){
                let obj = $(event.currentTarget);
                if (!obj.is('.active')) {
                    this.sex = val;
                }
            },
            attesChange(val, event){
                let obj = $(event.currentTarget);
                if (!obj.is('.active')) {
                    this.attes = val;
                }
            },
            serviceDayChange(val, event){
                let obj = $(event.currentTarget);
                if (!obj.is('.active')) {
                    this.serviceDay = val;
                }
            },

            periodsChange(key, event){
                let obj = $(event.currentTarget);
                if (!obj.is('.active')) {
                    if (key == 'normal') {
                        this.periods = {normal: true, am: false, pm: false, em: false};
                    } else {
                        this.periods.normal = false;
                        if (key == 'am') {
                            this.periods.am = true;
                        } else if (key == 'pm') {
                            this.periods.pm = true;
                        } else {
                            this.periods.em = true;
                        }
                    }
                } else {
                    let d = this.periods;
                    if (key == 'am') {
                        this.periods.am = false;
                    } else if (key == 'pm') {
                        this.periods.pm = false;
                    } else if (key == 'em') {
                        this.periods.em = false;
                    }

                    if (!d.am && !d.pm && !d.em) {
                        this.periods.normal = true;
                    }
                }
            },
            loadMenu(){
                this.$http.get(url.allCategory).then(
                    function (response) {
                        let data = response.data.data;
                        //
                        for (let i in data) {
                            this.parents.push({
                                name: data[i].name,
                                serviceId: data[i].serviceId
                            });
                        }
                        //
                        this.category = data;

                        //
                        this.getChildren(this.service.serviceId);
                    }
                )
            },//
            initFilter(){
                if (this.filter.price1) {
                    this.price1 = this.filter.price1;
                }
                if (this.filter.price2) {
                    this.price2 = this.filter.price2;
                }

                if (this.filter.startTime) {
                    this.startDate = this.filter.startTime;
                }

                if (this.filter.endTime) {
                    this.endDate = this.filter.endTime;
                }

                //
                if (this.filter.period) {
                    console.log('filter-period-> ' + this.filter.period);
                    let ps = this.filter.period.split(',');
                    this.periods.normal = false;
                    if (ps.indexOf('1') > -1) {
                        this.periods.am = true;
                    }
                    if (ps.indexOf('2') > -1) {
                        this.periods.pm = true;
                    }
                    if (ps.indexOf('3') > -1) {
                        this.periods.em = true;
                    }
                }

                if (this.filter.sex > -1) {
                    this.sex = this.filter.sex;
                }

                if (this.filter.attes > -1) {
                    this.attes = this.filter.attes;
                }

                if (this.filter.maxAge) {
                    if (this.filter.minAge == 0) {
                        this.age = 1;
                    } else if (this.filter.minAge == 25) {
                        this.age = 2;
                    } else if (this.filter.minAge == 35) {
                        this.age = 3;
                    }
                }

                if (this.filter.serviceDay) {
                    this.serviceDay = this.filter.serviceDay;
                }
            },
            getChildren(id){
                let _category = this.category;
                id = parseInt(id);
                if (id == 0) {
                    this.parent = {name: '', serviceId: 0};
                    this.children = [];
                    return;
                }
                let str = id + "";
                if (id != 99 && str.substring((str.length - 2), str.length) != "00") {
                    id = parseInt(id / 100) * 100;
                }
                for (let i = 0; i < _category.length; i++) {
                    if (_category[i].serviceId == id) {
                        this.parent = this.parents[i];
                        this.children = _category[i].subCategorys;
                    }
                }
            },
            selectDate (type){
                console.log("type:" + type);
                if (1 == type) {
                    this.$broadcast('init-time', type, '', this.startDate);
                } else if (2 == type) {
                    this.$broadcast('init-time', type, '', this.endDate);
                } else {
                    console.log("type not found:" + type);
                }
            }
        },
        events: {
            'startDate': function (date) {
                console.log(date);
                this.startDate = date;
                this.startDateFmt = dateFmtCHS(this.startDate);
            },
            'endDate': function (date) {
                this.endDate = date;
                this.endDateFmt = dateFmtCHS(this.endDate);
            }
        },
        watch: {
            'state': function () {
                let vm = this;
                setTimeout(function () {
                    vm.$broadcast('rescroll');
                }, 100);
                vm.serviceId = 0;
                vm.area = '330100';
                vm.sort = 0;
                vm.filter = {
                    price1: '',
                    price2: '',
                    startTime: '',
                    endTime: '',
                    period: '',
                    sex: -1,
                    attes: -1,
                    minAge: 0,
                    maxAge: 0,
                    serviceDay: -1
                };
                vm.keyword = '';
                vm.$emit('ready');
            }
        },
        components: {VHeader, VContent, VFooterBtn, VServiceList, VDateComponent}
    }
</script>
<style scoped>
    /*search-box*/
    .search-box {
        height: .4rem;
        position: absolute;
        top: .44rem;
        left: 0;
        right: 0;
        font-size: .14rem;
    }

    .search-box.b-alone {
        top: .84rem;
    }

    .search-box .warp {
        height: 100%;
        background-color: #fff;
        border-bottom: .01rem solid #e5e5e5;
    }

    .search-ul {
        height: 100%;
    }

    .search-ul li {
        text-align: center;
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
        border-right: .01rem solid #e5e5e5;
        position: relative;
    }

    .search-ul li.active {
        color: #00cda9;
    }

    .search-ul li:last-child {
        border: none;
    }

    .search-ul li:after {
        content: "";
        width: 0;
        height: .08rem;
        display: inline-block;
        border-top: .05rem solid #ccc;
        border-left: .05rem solid transparent;
        border-right: .05rem solid transparent;
        border-bottom: 0;
        margin-left: .02rem;
    }

    .search-ul li.active:after {
        border-top: 0;
        border-bottom: .05rem solid #00cda9;
        margin-bottom: .04rem;
    }

    .search-ul .img {
        position: absolute;
        width: .1rem;
        height: .06rem;
        top: .08rem;
        background-size: .1rem;
        right: .02rem;
    }

    .search-ul .img.down {
        background-image: url(../assets/img/icon/arrow-down.png);
    }

    .search-ul .active .img.down {
        background-image: url(../assets/img/icon/arrow-up.jpg);
    }

    /*search-form*/
    .search-form {
        position: absolute;
        left: 0;
        right: 0;
        font-size: .14rem;
        top: .84rem;
        height: 0;
        -webkit-transition: all .2s ease-in-out;
        transition: all .2s ease-in-out;
    }

    .search-form.b-alone {
        top: 1.24rem;
    }

    .search-form.active {
        height: 3.14rem;
    }

    .search-form .warp {
        height: 100%;
        background-color: #fff;
        overflow: scroll;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
    }

    .search-list .menu {
        float: left;
        height: 100%;
        overflow: scroll;
        overflow-y: auto;
    }

    .search-list .menu li {
        height: .45rem;
