<template>
    <div>
        <div :class="['time-box','z-index-max', adding ? 'active':'']">
            <div class="warp">
                <div class="top">
                    <div class="ps-abs mg-l10 font-14" @click="cancel"></div>
                    <div class="font-16">{{title}}</div>
                    <div class="ps-abs mg-r10 font-14 t-right" @click="add"></div>
                </div>
                <div class="box-selector">
                    <div class="box-mark"></div>
                    <div class="box-mark2"></div>
                    <div class="dis-flex boxes">
                        <div id="year" class="flex-1">
                            <ul>
                                <li>&nbsp;</li>
                                <li v-for="value in years" :class="value == year ? 'active' : ''">
                                    <span v-if="value!=''">{{value | zero-fill}}<span v-if="value!=''"></span></span>
                                </li>
                                <li>&nbsp;</li>
                                <li>&nbsp;</li>
                            </ul>
                        </div>
                        <div id="month" class="flex-1">
                            <ul>
                                <li>&nbsp;</li>
                                <li v-for="value in months" :class="value == month ? 'active' : ''">
                                    <span v-if="value!='' && value != -1">{{value | zero-fill}}</span>
                                </li>
                                <li>&nbsp;</li>
                                <li>&nbsp;</li>
                            </ul>
                        </div>
                        <div id="day" class="flex-2">
                            <ul>
                                <li>&nbsp;</li>
                                <li v-for="value in days" :class="value == day ? 'active' : ''">
                                    <span v-if="value!='' && value != -1">{{value | zero-fill}}</span>
                                </li>
                                <li>&nbsp;</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import VHeader from '../components/Header'
    import VContent from '../components/Content'
    import $ from 'jquery'
    import {iScroll} from '../assets/iscroll/iscroll'
    import {publish} from '../const'
    import {toast} from '../assets/js/toast'
    import {zeroFill} from  '../filter'
    import {DJUtils} from '../assets/js/common'

    export default{
        /*    props: {
         title: {type: String, default: '', twoWay: true},
         date: {type: String, default: new Date().Format('yyyy-MM-dd'), twoWay: true},
         },*/
        route: {
            data({from,to}){
                if (this.isReady) {
                    return {
                        state: !this.state
                    }
                }
            }
        },
        ready(){
            this.init();
            this.isReady = true;
        },
        data(){
            return {
                isReady: false,
                state: false,
                adding: false,
                title: '',
                year: '',
                month: '',
                day: '',
                date: '',
                years: [],
                months: [],
                days: [],
/*                _years:[],
                _months:[],
                _days:[],*/
                isReset: [true, true, true],//
                sObj: [],//
                isEdit:false,
                type: 1//1=2=3=
            }
        },
        methods: {
            init(){
                let vm = this;
                vm.initData();
                setTimeout(function () {
                    vm.loadScroll();
                    window.addEventListener("DOMContentLoaded", vm.loadScroll, false);
                }, 100);
            },
            cancel(){
                this.adding = false;
                toast.hide_mask();
            },
            add(){
                let date = this.year + '-' + this.month + '-' + this.day;
                if (1 == this.type) {
                    this.$dispatch('startDate', date);
                } else if (2==this.type){
                    this.$dispatch('endDate', date);
                }else if (3==this.type){
                    if (this.year == ''){
                        this.$dispatch('deadLine', '');
                    }else {
                        this.$dispatch('deadLine', date);
                    }
                }
                this.cancel();
            },
            loadScroll(){
                let vm = this;
                let s1 = new iScroll("year", {
                    hScroll: false, vScrollbar: false,
                    onScrollEnd: function () {
                        if (!vm.isReset[0] && vm.adding) {//
                            vm.getSelect(s1, '#year', 0);
                        } else {
                            vm.isReset[0] = false;
                        }
                    }
                });
                let s2 = new iScroll("month", {
                    hScroll: false, vScrollbar: false,
                    onScrollEnd: function () {
                        if (!vm.isReset[1] && vm.adding) {//
                            vm.getSelect(s2, '#month', 1);
                        } else {
                            vm.isReset[1] = false;
                        }
                    }
                });
                let s3 = new iScroll("day", {
                    hScroll: false, vScrollbar: false,
                    onScrollEnd: function () {
                        if (!vm.isReset[2] && vm.adding) {//
                            vm.getSelect(s3, '#day', 2);
                        } else {
                            vm.isReset[2] = false;
                        }
                    }
                });
                vm.sObj = [s1, s2, s3];//
                //
                vm.$once('destroy-scroll', function () {
                    s1.destroy();
                    s2.destroy();
                    s3.destroy();
                });
                vm.$on('init-time', function (type, title, date) {
                    console.log('type:' + type + ',title:' + title + ',date:' + date)
                    this.title = title;
                    this.date = date;
                    this.type = type;
                    vm.checkTime();//
                    /*vm.initDate(date);*/
                    let y1 = vm.getTimeIndex(vm.years, vm.year) * 45 * -1;
                    let y2 = vm.getTimeIndex(vm.months, vm.month) * 45 * -1;
                    let y3 = vm.getTimeIndex(vm.days, vm.day) * 45 * -1;
                    vm.isReset = [true, true, true];
                    vm.refsAll();
                    s1.scrollTo(0, y1, 100, false);
                    s2.scrollTo(0, y2, 100, false);
                    s3.scrollTo(0, y3, 100, false);
                });
            },
            initData(){
                let vm = this;
                let now = new Date();
                //
                vm.years = [];
                vm.months = [];
                vm.days = [];
                if (vm.type == 1 || vm.type == 2){
                    vm.years = [now.getFullYear(), now.getFullYear() + 1, ''];

                }else {
                    vm.years.push('');
                    for (let i = now.getFullYear(); i < now.getFullYear() + 100; i++) {
                        vm.years.push(i);
                    }
                }

                if (vm.year == ''){
                    vm.months.push(-1);
                    vm.days.push(-1);
                }else {
                    for (let i = 1; i <= 12; i++) {
                        vm.months.push(i);
                    }

                    for (let i = 1; i <= 31; i++) {
                        vm.days.push(i);
                    }
                }

                vm.years.push('');
                vm.months.push('');
                vm.days.push('');
            },
            getSelect(s, id, index){//scrollEnd  scroll id 
                let mark = $(".box-selector .box-mark").offset().top;
                let top = $(id + " li:first-child").offset().top;
                let plus = parseFloat((mark - top) % 45);
                let n = parseInt((mark - top) / 45);//nindex
                let before = $(id + " li.active").index() - 1;//index
                if (n == 1 && plus < 15) {
                    n = 0;
                } else {
                    n = n - 1;
                }
                if (n >= before) {//
                    if (plus > 15) {
                        n = n + 1;
                    }
                }
                n = n < 0 ? 0 : n;
                console.log(`${mark},${top},${plus},${n},${before}`);
                if (index == 0) {
                    if (n == this.years.length) {
                        n = n - 1;
                    }
                    this.year = this.years[n];
                } else if (index == 1) {
                    if (n == this.months.length) {
                        n = n - 1;
                    }
                    this.month = this.months[n];
                } else {
                    if (n == this.days.length) {
                        n = n - 1;
                    }
                    this.day = this.days[n];
                }
                if(index == 0 || index == 1){//daydays
                    this.setMonthsAndDays(index);
                }
                console.log(`:${index}`);
                this.isReset[index] = true;//
                s.scrollTo(0, (n * 45 * -1), 10, false);

            },
            setSelectTime(){
                /*        this.start = zeroFill(this.) + ":" + zeroFill(this.minute);
                 let min = this.hour * 60 + this.minute + this.after * 60;
                 let h = parseInt(min / 60);
                 let m = min % 60;
                 this.end = zeroFill(h) + ":" + zeroFill(m);*/
            },
            getTimeIndex(items, item){
                for (let i in items) {
                    if (items[i] == item) {
                        return parseInt(i);
                    }
                }
            },
            checkTime(){
                let vm = this;
                if(this.date){
                    //
                    let dateArr = this.date.split('-');
                    this.year = dateArr[0];
                    this.month = dateArr[1];
                    this.day = dateArr[2];
                }else{
                    //
                    let now = new Date();
                    if (this.type == 3){
                        this.year = '';
                        this.month = -1;
                        this.day = -1;
                    }else {
                        this.year = now.getFullYear();
                        this.month = now.getMonth() + 1;
                        this.day = now.getDate();
                    }
                }

                //
                vm.setMonthsAndDays();
/*

                vm.scrollChange();*/
                vm.checkOk();
            },
            checkOk(){
                $("#year ul").css({height: parseFloat(this.years.length * 0.45 + 0.9) + 'rem'});
                $("#month ul").css({height: parseFloat(this.months.length * 0.45 + 0.9) + 'rem'});
                $("#day ul").css({height: parseFloat(this.days.length * 0.45 + 0.9) + 'rem'});
                toast.hide_mask();
                toast.mask(null, true);
                this.adding = true;
            },
            scrollChange(index){
                let now = new Date();
                let isEdit = false;
                if (this.date) {
                    let dateArr = this.date.split('-');
                    this.year = dateArr[0];
                    this.month = dateArr[1];
                    this.day = dateArr[2];
                    isEdit = true;
                } else {
                    if (this.type == 3){
                        this.year = '';
                        this.month = '';
                        this.day = '';
                    }else {
                        this.year = now.getFullYear();
                        this.month = now.getMonth() + 1;
                        this.day = now.getDate();
                    }
                }

                let _months = [];
                let _days = [];
                if (isEdit) {//

                } else {//
                if (this.year == ''){
                        this.months = [];
                        this.days = [];
                    }else if (now.getFullYear() == this.year) {
                        let _indexm = this.months.indexOf(now.getMonth() + 1);
                        this.months.splice(0, _indexm);

                        if (now.getDate() == this.day) {
                            let _indexd = this.days.indexOf(now.getDate());
                            this.days.splice(0, _indexd);
                        }
                    }
                }

            },
            setMonthsAndDays(index){
                let now = new Date();
                let vm = this;
                //
                vm.initData();

                if (vm.year != ''){
                    let totalDays =  DJUtils.getCurrMonthDays(this.year,this.month);
                    console.log('currMonth days:'+ totalDays);
                    if (totalDays != vm.days.length){
                        //
                        vm.days.splice(totalDays,vm.days.length-totalDays-1);
                        console.log(vm.days.toString());
                    }
                }

                //
                if (now.getFullYear() == vm.year) {
                    //
                    let _indexm = vm.months.indexOf(now.getMonth() + 1);
                    vm.months.splice(0, _indexm);

                    console.log(now.getMonth() + ":" + vm.month + ";" + now.getDate() + ":" + vm.day);
                    if ((now.getMonth()+1) == vm.month) {//
                        let _indexd = vm.days.indexOf(now.getDate());
                        vm.days.splice(0, _indexd);
                    }
                }

                console.log('adding:'+ this.adding)
                if (this.adding) {
                    if (index == 0) {//
                        $("#month ul").css({height: parseFloat(this.months.length * 0.45 + 0.9) + 'rem'});
                        $("#day ul").css({height: parseFloat(this.days.length * 0.45 + 0.9) + 'rem'});
//                        if ($.inArray(this.day, this.days) < 0) {
//                            this.day = this.days[0];
//                        }

                        if ($.inArray(this.month, this.months) < 0) {
                            this.month = this.months[0];
                        }
                        this.sObj[1].refresh();
//                        this.sObj[2].refresh();
                        this.resizeUl(1);
//                        this.resizeUl(2);

                    } else if (index == 1) {//
                        $("#day ul").css({height: parseFloat(this.days.length * 0.45 + 0.9) + 'rem'});
                        if ($.inArray(this.day, this.days) < 0) {
                            this.day = this.days[0];
                        }
                        this.sObj[2].refresh();
                        this.resizeUl(2);
                    }
                }
            },
            refsAll()
            {
                let scrolls = this.sObj;
                for (let i = 0; i < scrolls.length; i++) {
                    scrolls[i].refresh();
                }
            },
            resizeUl(index){
                console.log('resizeUI:'+this.days);
                let y = 0;
                if (index == 0) {
                    y = this.getTimeIndex(this.years, this.year);
                } else if (index == 1) {
                    y = this.getTimeIndex(this.months, this.month);
                } else {
                    y = this.getTimeIndex(this.days, this.day);
                }
                console.log(`resizeUl index:${index} Y:${y}`)
                this.sObj[index].scrollTo(0, y * 45 * -1, 100, false);
            }
        },
        watch: {
            'state': function () {
                this.times = [];//{start: '', end: ''}
                this.type = 0;
                this.workTime = '';
                this.adding = false;
                this.start = '08:30';
                this.end = '09:30';
                this.hour = 8;
                this.minute = 30;
                this.after = 1;
                this.time = {start: '', end: ''};
                this.isReset = [true, true, true];
                this.loadQuery();
            }
        },
        destroyed()
        {
            this.$emit('destroy-scroll');
        }
        ,
        components: {
            VHeader,
            VContent
        }
    }
</script>
<style scoped>
    .timeList {
        background-color: #fff;
        border-bottom: .01rem solid #e5e5e5;
        font-size: .14rem;
        text-align: left;
    }

    .timeList li {
        border-bottom: .01rem solid #e5e5e5;
        position: relative;
        height: .45rem;
        margin-left: .1rem;
        padding-right: .1rem;
        line-height: .45rem;
    }

    .timeList li:last-child {
        border-bottom: none;
    }

    li .title {
        float: left;
        width: 25%;
    }

    li .edit {
        float: left;
        width: 70%;
    }

    .edit .text {
        color: #333;
    }

    .edit .text.none {
        color: #999;
    }

    .img.transfer {
        position: absolute;
        right: .1rem;
        top: .15rem;
        width: .08rem;
        height: .12rem;
        background-size: .08rem;
        background-image: url(../assets/img/icon/arrow-right.png);
    }

    .img.del, .img.write {
        position: absolute;
        top: .15rem;
        width: .14rem;
        height: .14rem;
        background-size: .14rem;
    }

    .img.del {
        right: .4rem;
        background-image: url(../assets/img/icon/del.png);
    }

    .img.write {
        right: .1rem;
        background-image: url(../assets/img/icon/edit.png);
    }

    .point {
        margin-top: .2rem;
        text-align: center;
    }

    .point .title {
        font-size: .14rem;
        text-indent: .2rem;
        position: relative;
        margin: 0 auto;
        width: 1.2rem;
    }

    .point .help {
        margin-top: .05rem;
    }

    .img.add {
        position: absolute;
        top: 0;
        left: 0;
        width: .19rem;
        height: .19rem;
        background-size: .19rem;
        background-image: url(../assets/img/icon/add.png);
    }

    .time-box {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: 0;
        -webkit-transition: all .3s ease-in-out;
        transition: all .3s ease-in-out;
    }

    .time-box.active {
        height: 2.44rem;
    }

    .time-box .warp {
        height: 100%;
        text-align: center;
    }

    .time-box .top {
        height: .44rem;
        line-height: .44rem;
        background-color: #3c506b;
        color: #fff;
        text-align: center;
    }

    .top .t-right {
        right: 0;
        top: 0;
    }

    .time-box .mid {
        height: .5rem;
        background-color: #f3f3eb;
        color: #fa3c46;
    }

    .time-box .box-selector {
        background-color: #fff;
        height: 2rem;
        position: relative;
    }

    .box-selector .box-mark {
        position: absolute;
        top: .55rem;
        left: 0;
        right: 0;
        height: .45rem;
        border-top: .01rem solid #e5e5e5;
        border-bottom: .01rem solid #e5e5e5;
    }

    .box-selector .box-mark2 {
        position: absolute;
        top: 1rem;
        left: 0;
        right: 0;
        height: .45rem;
        border-bottom: .01rem solid #e5e5e5;
    }

    .box-selector .boxes {
        height: 100%;
        position: relative;
    }

    .boxes > div {
        position: absolute;
        top: .1rem;
        bottom: .1rem;
        left: 0;
        width: 33%;
        overflow: hidden;
    }

    .boxes > div:nth-child(2) {
        left: 33%;
    }

    #day {
        left: 66%;
        width:34%;
    }

    .boxes ul {
        color: #999;
        height: 100%;
        z-index: 9999;
    }

    .boxes li {
        height: .45rem;
        line-height: .45rem;
    }

    .boxes li.active {
        color: #03c5a3;
    }
</style>




