<template>
  <div id="wrapper">
    <div id="scroller">
      <div id="pullDown" v-if="scroll">
        <span class="pullDownLabel" v-show="firstLoad && !isEdit"></span>
        <span class="pullDownIcon"></span>
      </div>
      <slot name="title" v-if="items.length > 0"></slot>

      <ul class="service-box" v-if="items.length > 0">
        <li v-for="item in items" @click="linkDetail(item.id)" class="service">

          <div v-if="isEdit" class="check-box">
            <div class="control-box">
              <input type="checkbox" value="{{item.id}}" @click="dispatchChecked($event)" class="ctrl-box"/>
              <span :class="['img',jobIds.indexOf(item.id) > -1 ? 'checked' : 'none']"></span>
            </div>
          </div>

          <ul :class="['box','tran-all-3',isEdit ? 'isEdit' : '']">
            <li class="user-info">
              <span class="img icon" style="background-image:url({{item.icon | img-host}})" v-if="item.icon"></span>
              <span :class="['img','icon',item.sex == 1 ? 'sir' : 'lady']" v-else></span>
              <span class="img major" v-if="item.isMajor"></span>
              <p class="username ellipsis">{{item.nickName}}</p>
              <!-- <ul class="sex-box">
                <li>
                  <span :class="['img','sex',item.sex == 1 ? 'sir' : 'lady']"></span>
                </li>
                <li><p class="age">{{item.age}}</p></li>
              </ul> -->
            </li>
            <li class="service-info">
              <p class="name ellipsis">{{item.name | ellipsis 9}}
                <span :class="['sex-age','img',item.sex == 1 ? 'sir' : 'lady']">
                  {{item.age}}
                </span>
              </p>
              <p class="work-time ellipsis">{{item.workTime}}</p>
              <p class="work-line" v-if="item.timeless"></p>
              <p class="work-line" v-else>{{item.startTime}} {{item.workDays}}</p>

              <span class="price">{{item.price}}<span class="font-12">/</span></span>
              <!-- <span class="orders">{{item.workArea | area-filter}}</span> -->
            </li>
          </ul>
        </li>
      </ul>
      <div class="none_bg" v-if="items.length==0 && scroll && firstLoad">
        <span :class="['img',link_name]"></span>
        <p v-if="link_name == 'list'" class="remark">
          ,...
        </p>
        <p v-else class="remark">
          
        </p>
      </div>

      <div id="pullUp" v-if="scroll">
        <span class="pullUpLabel" v-show="firstLoad && !isEdit && items.length > 0"></span>
        <span class="pullUpIcon"></span>
      </div>
    </div>
  </div>
</template>
<script>
  import {url} from '../const'
  import $ from 'jquery'
  import {loading} from '../assets/js/iscroll'
  import {toast} from '../assets/js/toast'

  export default{
    props: {
      user_id: {type: Number, default: 0},
      link_name: {type: String, default: 'list'},
      service_id: {type: Number, default: 0, twoWay: true},
      sort: {type: Number, default: 0, twoWay: true},
      area: {type: String, default: '330100', twoWay: true},
      keyword: {type: String, default: '', twoWay: true},
      filter: {type: Object, default: {}, twoWay: true}
    },
    ready(){
      this.$on('service-list-init', this.init);
      this.$emit('service-list-init');
      this.$on('rescroll', this.initScroll);
      this.$on('reload-child', function () {
        this.$http.get(this.link[this.link_name], this.linkData).then(
                function (response) {
                  let data = response.data.data;
                  if (data) {
                    this.items = data;
                  } else {
                    this.items = [];
                  }
                  this.$dispatch('load-back', 1);
                }, function (response) {
                  this.$dispatch('load-back', -1);//
                }
        )
      })
    },
    data()
    {
      return {
        items: [],
        page: 1,
        row: 10,
        pages: 1,
        t: new Date().getTime(),
        linkData: {},
        scroll: false,
        link: [],
        isEdit: false,//
        jobIds: [],
        firstLoad: false
      }
    },
    computed: {},
    methods: {
      init(){
        this.link = {
          his: url.servicesHis,
          recommend: url.serviceRecommend,
          list: url.serviceList,
          favorite: url.favorites
        };
        if (this.link_name == 'list') {
          //
          let filterProps = ['price1', 'price2', 'startTime', 'endTime', 'period', 'sex', 'attes', 'minAge', 'minAge', 'serviceDay'];
          //
          let query = this.$route.query;
          let t = /^(\+|-)?\d+($|\.\d+$)/;
          for (let i in query) {
            if (i == 'id') {
              continue;
            }
            if (query[i]) {
              if (filterProps.indexOf(i) > -1) {
                if (t.test(query[i]) && i != 'period') {
                  this.filter[i] = parseInt(query[i]);
                } else {
                  this.filter[i] = query[i];
                }
              } else {
                if (t.test(query[i])) {
                  this[i] = parseInt(query[i]);
                } else {
                  this[i] = query[i];
                }
              }
            }
            if (i == 'sex' && query[i] == '0') {
              this.filter[i] = 0;
            }
            if (i == 'attes' && query[i] == '0') {
              this.filter[i] = 0;
            }
          }

          if (!query.id) {
            this.service_id = 0;
          } else {
            this.service_id = parseInt(query.id);
          }
          this.listData();
          this.scroll = true;
          $("#wrapper").css({
            position: 'absolute'
          });
        } else if (this.link_name == 'favorite') {
          this.favoriteData();
          this.scroll = true;
          $("#wrapper").css({
            position: 'absolute'
          });
          //&
          this.$on('edit-status', function (isEdit, remove, firstLoad) {
            this.isEdit = isEdit;
            if (!this.isEdit) {//
              this.jobIds = [];
              if (remove === true) {
                this.down();
              }
              $("#wrapper").animate({scrollTop: '0px'}, 10);
              if (!firstLoad) {
                this.initScroll();
              }
            } else {// scroll
              loading.destroy();
              $("#wrapper").css({overflow: 'scroll', overflowY: 'auto', WebkitOverflowScrolling: 'touch'});
            }
          });
          this.$on('check-all', function (isAll) {
            this.jobIds = [];
            if (isAll) {
              for (let i = 0; i < this.items.length; i++) {
                this.jobIds.push(parseInt(this.items[i].id));
              }
            }
            this.dispatchChecked();
            $(".check-box input[type='checkbox']").prop("checked", isAll);
          });
          this.$watch('items', function () {
            this.$dispatch('item-len', this.items.length);
          });
        } else {
          this.homeData();
        }
        toast.loading();
        this.addEvent();
        this.$http.get(this.link[this.link_name], this.linkData).then(
                function (response) {
                  this.firstLoad = true;
                  let data = response.data.data;
                  if (data) {
                    this.items = data;
                  } else {
                    this.items = [];
                  }
                  this.pages = response.data.totalPage;
                  if (this.scroll) {
                    this.resetScroll();
                    loading.refs(this.page, this.pages);
                  }
                  this.$dispatch('load-back', 1);
                }, function (response) {
                  toast.alert('');
                }
        )
      },
      down(){
        this.page = 1;
        this.t = new Date().getTime();
        this.listData();
        this.$http.get(this.link[this.link_name], this.linkData).then(
                function (response) {
                  let data = response.data.data;
                  /*console.log(JSON.stringify(data));*/
                  if (data) {
                    this.items = data;
                  } else {
                    this.items = [];
                  }
                  this.pages = response.data.totalPage;
                  this.resetScroll();
                  loading.refs(this.page, this.pages);
                }, function (response) {
                  loading.downError();
                }
        )
      },
      up(){
        this.page++;
        this.listData();
        this.$http.get(this.link[this.link_name], this.linkData).then(
                function (response) {
                  let data = response.data.data;
                  /*console.log(JSON.stringify(data));*/
                  if (data) {
                    for (let i in data) {
                      this.items.push(data[i]);
                    }
                  }
                  this.resetScroll();
                  loading.refs(this.page);
                }, function (response) {
                  loading.upError();
                }
        )
      },
      addEvent(){
        let vm = this;
        if (vm.scroll) {
          this.initScroll();
          vm.$on('refs', function () {
            toast.loading();
            loading.top();
          });
        }
      },
      initScroll(){
        let vm = this;
        let name = "service-list";
        if (vm.link_name == 'favorite') {
          name = "favorite";
        }
        let hasIn = false;
        if (loading.hasScroll()) {
          if (loading.getName() == name) {
            hasIn = true;
          } else {
            loading.destroy();
          }
        }
        if (!hasIn) {
          vm.resetScroll();
          loading.ready({
            wrapper: 'wrapper',
            down: 'pullDown',
            downText: '.pullDownLabel',
            downFn: vm.down,
            up: 'pullUp',
            upText: '.pullUpLabel',
            upFn: vm.up,
            pages: vm.pages,
            name: name
          });
        }
      },
      listData(){
        let _filter = this.filter;
        this.linkData = {
          currentPage: this.page,
          pageSize: this.row,
          t: this.t,
          sort: this.sort,
          serviceId: this.service_id,
          area: this.area,
          keyword: this.keyword,
          price1: _filter.price1,
          price2: _filter.price2,
          startTime: _filter.startTime,
          endTime: _filter.endTime,
          period: _filter.period,
          sex: _filter.sex,
          attes: _filter.attes,
          minAge: _filter.minAge,
          maxAge: _filter.maxAge,
          serviceDay: _filter.serviceDay
        }
      },
      favoriteData(){
        this.linkData = {
          currentPage: this.page,
          pageSize: 10,
          t: this.t
        }
      },
      homeData(){
        this.linkData = {
          userId: this.user_id,
          currentPage: this.page,
          pageSize: 999,
          t: this.t
        }
      },
      resetScroll(){
        let len = this.items.length * 1.25;
        let wLen = $(window).height();
        if (this.link_name == 'list') {
          wLen = wLen - 84;
        } else {
          wLen = wLen - 89;
        }
        if ((len + 0.3) < wLen / 100) {
          $("#scroller").css({height: wLen + "px"});
        } else {
          $("#scroller").css({height: parseFloat(len - 0.1 + 0.4) + "rem"});
        }
      },
      linkDetail(id){
        if (!this.isEdit) {
          window.location.href = `#/service/${id}`;
        }
      },
      dispatchChecked(event){
        if (event) {
          let check = $(event.currentTarget);
          if (check.is(":checked")) {
            this.jobIds.push(parseInt(check.val()));
          } else {
            this.jobIds.splice($.inArray(parseInt(check.val()), this.jobIds), 1);
          }
        }

        this.$dispatch('job-checked', this.jobIds);
        this.$dispatch('is-all', this.jobIds.length == this.items.length);
      }
    },
    //keep-alive  
    destroyed(){
      // if (this.scroll) {
      //   loading.destroy();
      // }
    }
  }
</script>
<style scoped>
  .service-box {
    position: relative;
    width: 100%;
  }

  .service-box .service {
    background-color: #fff;
    height: 1.15rem;
    margin-bottom: .1rem;
    border-top: .01rem solid #f2f2f2;
    border-bottom: .01rem solid #f2f2f2;
    position: relative;
  }

  .service .check-box {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .check-box .control-box {
    width: 100%;
    height: 100%;
  }

  .control-box .img {
    position: absolute;
    left: .1rem;
    top: .3rem;
    width: .18rem;
    height: .18rem;
    background-size: .18rem;
  }

  .img.none {
    background-image: url(../assets/img/icon/select03.png);
  }

  .img.checked {
    background-image: url(../assets/img/icon/selected03.png);
  }

  .service-box .service:last-child {
    margin-bottom: 0;
  }

  .service-box .box {
    padding: .15rem .1rem;
    position: relative;
    height: 100%;
    margin-left: 0;
  }

  /*.service-box .box:hover {*/
  /*background-color: #f1f1f1;*/
  /*}*/

  .box.isEdit {
    margin-left: .3rem;
  }

  .box li {
    float: left;
    text-align: center;
    color: #999;
    height: 100%;
  }

  li.user-info {
    font-size: .12rem;
    position: absolute;
    height: auto;
    left: .15rem;
    width: .7rem;
  }

  li.service-info {
    font-size: .12rem;
    text-align: left;
    line-height: .29rem;
    position: relative;
    width: 100%;
  }

  .user-info .username {
    color: #333;
    margin-top: .05rem;
  }

  .user-info .icon {
    margin: 0 auto;
    border-radius: 50%;
    width: .6rem;
    height: .6rem;
    background-size: .6rem;
  }

  .user-info .img.major{
    position: absolute;
    top: .48rem;
    left: .05rem;
    width: .6rem;
    height: .15rem;
    background-size: .6rem;
    background-image: url(../assets/img/icon/major.png);
  }

  .service-info .name {
    font-size: .17rem;
    color: #333;
  }

  .sex-age {
    padding: .01rem .06rem;
    border-radius: .03rem;
    color: #fff;
    font-size: .12rem;
    display: inline-block;
    margin-left: .1rem;
    height: .2rem;
    vertical-align: text-bottom;
    padding-top: 0;
    line-height: 1.8;
  }

  .sex-age.sir {
    background-color: #52c0f6;
  }

  .sex-age.lady {
    background-color: #fc85a1;
  }

  .sex-age:before {
    content: '';
    width: .09rem;
    height: .12rem;
    background-size: .09rem;
    display: inline-block;
    margin-right: .02rem;
    vertical-align: text-top;
  }

  .sex-age.sir:before {
    background-image: url(../assets/img/icon/i_sir.png);
  }

  .sex-age.lady:before {
    background-image: url(../assets/img/icon/i_lady.png);
  }

  .name .area {
    font-size: .12rem;
    color: #999;
  }

  .service-info p {
    margin-left: .9rem;
  }

  .service-info .work-time {
    font-size: .15rem;
    color: #666;
  }

  .service-info .price {
    color: #fa5a3c;
    font-size: .15rem;
    position: absolute;
    right: 0;
    bottom: -.05rem;
  }

  .service-info .orders {
    position: absolute;
    right: 0;
    bottom: -.05rem;
  }

  .img.list {
    background-image: url(../assets/img/bg/null_icon02.png);
  }

  .img.favorite {
    background-image: url(../assets/img/bg/null_icon03.png);
  }
</style>



